.portfolio {
  display: flex;
  flex-direction: column;
  .portfolio-header {
    padding: 16px 32px;
    background-color: black;
    //height: 100px;
    z-index: 100px;
    &-buttons-tab-list {
      display: flex;
      justify-content: flex-end;
      $borderRadius: 100px;
      align-items: center;
      margin: 0px;
      padding: 0px;
      box-shadow: 0 1px white;
      border-radius: $borderRadius;
      height: 32px;
      width: fit-content;
      margin-left: auto;
      .button-tab {
        cursor: pointer;
        display: flex;
        justify-content: center;
        //width: 100%;
        padding: 7px;
        border-radius: $borderRadius;
        font-size: 12px;
        line-height: 16px;
        //max-width: calc(100% / var(--numberTabs));
        text-align: center;
        span {
          // @include text-overflow-lines(1);
          color: aquamarine;
          font-family: "Courier New", Courier, monospace;
          width: 100%;
          text-align: center;
        }
        &-selected {
          background-color: rgb(22, 26, 27);
          box-shadow: 0 1px aquamarine;
          span {
            color: green;
            font-family: "Courier New", Courier, monospace;
          }
        }
      }
    }
  }
  .portfolio-body {
    background-color: aqua;
    height: 800px;
  }
  .portfolio-footer {
    background-color: green;
    height: 100px;
  }
}
